import React from "react";
import CardDog from "./CardDog.js";

/** en el componente CardDog de la linea 15 hay que colocar el src de la imagen de los perros
 * un text
 * una label
 * y el path a /dogs
 */

function Dogs() {
  return (
    <div className="card">
      <h1>See Dogs</h1>
      <div className="card__container">
        <div className="card__wrapper">
          <ul className="card_item">
            <CardDog path="/dogs"></CardDog>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Dogs;
